import React, { Component } from "react";
import Masonry from 'react-masonry-component';
import ItemCard from "./itemcard";

const masonryOptions = {
  transitionDuration: 1,
  itemSelector: '.component--item_card',
  columnWidth: '.component--item_card',
  gutter: 30,
  percentPosition: true,
  horizontalOrder: true,
  transitionDuration: '0.8s'
};

const imagesLoadedOptions = { background: '.my-bg-image-el' }

class Listpage extends Component {
  id = 1;
  state = {};
  render() {
    const { Itemcard } = this.props;
    return (
      <section id="Projects">
        <h1>프로젝트</h1>
        <Masonry
            className={'ptfListWrap'}
            elementType={'ul'} // default 'div'
            options={masonryOptions}
            disableImagesLoaded={false} // default false
            updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
            imagesLoadedOptions={imagesLoadedOptions} // default {}
        >
        {Itemcard &&
            Itemcard.map((itemdata, insertIndex) => {
              return (
                <ItemCard
                  key={insertIndex}
                  ImageURL={itemdata.ImageURL}
                  WhereamI={itemdata.WhereamI}
                  WhatIDid={itemdata.WhatIDid}
                  PrjName={itemdata.PrjName}
                  Hashtags={itemdata.Hashtags}
                  LinkURL={itemdata.LinkURL}
                />
              );
          }
          )}

        </Masonry>
      </section>
    );
  }
}
export default Listpage;
